export function getValueFromQuotationStr(value: string): string {
    return value.slice(1, -1);
}

export function getValueFromBlockComment(value: string): string {
    return value
        .slice(2, -2)
        .split(/\n/)
        .map(s => s.trim())
        .map(s => s.replace(/\*+/, ''))
        .map(s => s.trim())
        .filter(s => s.length > 0)
        .join('\n');
}

export function getValueFromLineCommnet(value: string): string {
    return value.slice(2).trim();
}
