import * as moo from 'moo';
import {
    getValueFromBlockComment,
    getValueFromLineCommnet,
    getValueFromQuotationStr,
} from './helper';

const lexer = moo.compile({
    LEFT_BRACE: '{', // {
    RIGHT_BRACE: '}', // }
    LEFT_BRACKET: '[', // [
    RIGHT_BRACKET: ']', // ]
    COLON: ':', // :
    COMMA: ',', // ,
    STRING: [
        {
            match: /"(?:\\["\\]|[^\n"\\])*"/,
            value: getValueFromQuotationStr,
        },
        {
            match: /'(?:\\['\\]|[^\n'\\])*?'/,
            value: getValueFromQuotationStr,
        },
    ],
    NUMBER: [
        { match: /0{1}/ },
        { match: /[1-9]\d*\.?\d*/ },
        { match: /-[0-9]\d*\.?\d*/ },
        { match: /0\.\d*/ },
    ],
    IDENTIFIER: {
        match: /[a-zA-Z_][a-zA-Z0-9_]*/,
        type: moo.keywords({
            NULL: 'null',
            TRUE: 'true',
            FALSE: 'false',
        }),
    },
    BLOCK_COMMENT: {
        match: /\/\*[\s\S]*?\*\//,
        value: getValueFromBlockComment,
        lineBreaks: true,
    },
    LINE_COMMENT: {
        match: /\/\/[^\n]*/,
        value: getValueFromLineCommnet,
    },
    LINE_BREAK: { match: /\n/, lineBreaks: true },
    WHITE_SPACE: { match: /\s+/, lineBreaks: true },
});

export class Lexer {
    static create(code: string) {
        return new this(code);
    }

    tokens: moo.Token[] = [];
    pos = -1;

    get current() {
        return this.tokens[this.pos];
    }

    constructor(readonly code: string) {
        lexer.reset(code);

        this.tokens = Array.from(lexer);
    }

    walk() {
        if (this.pos === -1 || this.tokens[this.pos]) {
            return this.tokens[++this.pos];
        }

        return this.tokens[this.pos];
    }
}
